import { useState } from 'react'
import { connect } from 'react-redux'
import { backActionCreator, calculationThunkCreator, cleanStringActionCreator, insertValueActionCreator } from '../data/CalculatorReducer'
import './Calculator.css'

const Calculator = (props) => {
    /* 
    result = eval(state.numbers.map(item => return item.value))
    чтобы перемножить на курс - state.numbers.map(item => проверка наличия типа item.type=number item.value *course)
    */
    const [formData, setFormData] = useState("")
    const [disableSymbols, setDisableSymbols] = useState(true)
    const [selectedPair, setPair] = useState('')
    const [multiplyMode, setMultuplyMode] = useState(false)
    const [equalShow, setEqualShow] = useState(false)
    const handleChange = (e) => {
        const value = e.target.value
        setPair(value)
    }
    const handleClick = (value) => {
        let newCell = {
            type: "",
            value: null
        }
        if (value >= 0 | value === ".") {
            setFormData(formData + value)
            setDisableSymbols(false)
        }
        if (value === "+" | value === "-" | value === "(" | value === ")") {
            if (!disableSymbols) {
                setDisableSymbols(true)
                newCell = {
                    ...newCell,
                    type: multiplyMode ? "multiplier" : "number",
                    value: formData
                }
                props.insertValueActionCreator(newCell)
                setFormData("")
                newCell = {
                    ...newCell,
                    type: "symbol",
                    value: value
                }
                props.insertValueActionCreator(newCell)
                setMultuplyMode(false)
            }
        }
        if (value === "*" | value === "/") {
            setMultuplyMode(true)
            if (!disableSymbols) {
                setDisableSymbols(true)
                newCell = {
                    ...newCell,
                    type: multiplyMode ? "multiplier" : "number",
                    value: formData
                }
                props.insertValueActionCreator(newCell)
                setFormData(value)
                newCell = {
                    ...newCell,
                    type: "multiplier",
                    value: value
                }
                props.insertValueActionCreator(newCell)
                setFormData("")
            }
        }
        if (value === 'clear') {
            setMultuplyMode(false)
            setFormData('')
            setDisableSymbols(true)
            props.cleanStringActionCreator()
            setEqualShow(false)
        }
        if (value === 'back') {
            formData ? setFormData('') : props.backActionCreator()
        }
        if (value === 'equal') {
            if (selectedPair !== '' && selectedPair !== 'Выберите валюту') {
                if (!disableSymbols) {
                    setDisableSymbols(true)
                    newCell = {
                        ...newCell,
                        type: multiplyMode ? "multiplier" : "number",
                        value: formData
                    }
                    props.insertValueActionCreator(newCell)
                    setFormData('')
                    setDisableSymbols(true)
                    props.calculationThunkCreator(selectedPair)
                    setEqualShow(true)
                }
            } else alert('Выберите валюту')
        }
        //режим умножения
    }
    return (
        <>
            <div className="form">
                <label htmlFor="currencySelect">Выберите валюту для расчета</label>
                <select
                    name="currencySelect"
                    onChange={handleChange}
                >
                    <option>Выберите валюту</option>
                    <option>USD</option>
                    <option>EUR</option>
                    <option>BYN</option>
                    <option>UAH</option>
                    <option>AMD</option>
                    <option>MDL</option>
                    <option>AZN</option>
                    <option>TJS</option>
                    <option>UZS</option>
                    <option>KZT</option>
                    <option>TRY</option>
                    <option>SEK</option>
                    <option>CZK</option>
                </select>
            </div>
            <div className="calculatorWrap">
            <div class="calculator">
                <div className="item form">{props.inputString.map(item => item.value)}{formData}</div>
                <div className="item clean" onClick={() => handleClick("clear")}>C</div>
                <div className="item back" onClick={() => handleClick("back")}>&larr;</div>
                <div className="item" onClick={() => handleClick("+")}>+</div>
                <div className="item" onClick={() => handleClick("-")}>-</div>
                <div className="item" onClick={() => handleClick("*")}>&times;</div>
                <div className="item" onClick={() => handleClick("/")}>&divide;</div>
                <div className="item" onClick={() => handleClick("7")}>7</div>
                <div className="item" onClick={() => handleClick("8")}>8</div>
                <div className="item" onClick={() => handleClick("9")}>9</div>
                <div className="item" onClick={() => handleClick("(")}>(</div>
                <div className="item" onClick={() => handleClick("4")}>4</div>
                <div className="item" onClick={() => handleClick("5")}>5</div>
                <div className="item" onClick={() => handleClick("6")}>6</div>
                <div className="item" onClick={() => handleClick(")")}>)</div>
                <div className="item" onClick={() => handleClick("1")}>1</div>
                <div className="item" onClick={() => handleClick("2")}>2</div>
                <div className="item" onClick={() => handleClick("3")}>3</div>
                <div className="item equal" onClick={() => handleClick("equal")}>=</div>
                <div className="item zero" onClick={() => handleClick("0")}>0</div>
                <div className="item" onClick={() => handleClick(".")}>.</div>
            </div>
            <div className="result">
                <p>{props.newValues} {equalShow? ' = ' : null} {props.result}</p>
            </div>
            </div>
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        inputString: state.calculator.inputString,
        newValues: state.calculator.newValues,
        result: state.calculator.result
    }
}


export default connect(mapStateToProps, {
    insertValueActionCreator,
    cleanStringActionCreator,
    backActionCreator,
    calculationThunkCreator
})(Calculator)