import { currenciesAPI } from "../api/api"
import { BACK, CLEAN_STRING, CONVERSION, INSERT_VALUE, SET_CURRENCIES } from "./types"

const initialState = {
    currencies: {},
    inputString: [],
    newValues: [],
    result: ""
}

export const CalculatorReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_CURRENCIES:
            return {
                ...state,
                currencies: action.currencies
            }
        case INSERT_VALUE:
            return {
                ...state,
                inputString: [...state.inputString, action.number]
            }
        case CLEAN_STRING:
            return {
                ...state,
                inputString: [],
                newValues: [],
                result: []
            }
        case CONVERSION:
            let newVal = state.inputString.map(item => item.type === 'number' ? (item.value * state.currencies[action.pair]).toFixed(2) : item.value).join(' ')
            let res = eval(newVal).toFixed(2)
            return {
                ...state,
                newValues: newVal,
                result: res
            }
        case BACK:
            return {
                ...state, 
                inputString: [...state.inputString.slice(0, state.inputString.length -1)]
            }
        default: return state
    }
}

export const insertValueActionCreator = (number) => {
    return {
        type: INSERT_VALUE, number
    }
}

export const cleanStringActionCreator = () => {
    return {
        type: CLEAN_STRING
    }
}

const conversionActionCreator = (pair) => {
    return {
        type: CONVERSION, pair
    }
}

const setCurrenciesActionCreator = (currencies) => {
    return {
        type: SET_CURRENCIES, currencies
    }
}

export const backActionCreator = () => {
    return {
        type: BACK
    }
}

export const calculationThunkCreator = (pair) => async (dispatch) => {
    let response = await currenciesAPI.getCurrencies()
    dispatch(setCurrenciesActionCreator(response))
    dispatch(conversionActionCreator(pair))
}

